.payment-icons__{
  &heading{
    border-bottom: solid 1px $brand-col-1;
    font-weight: 500;
    margin-bottom: $inuit-global-spacing-unit-small;
  }
  &list{
    @extend .o-list-inline;
    @include mq($until : tablet){
      text-align: center;
    }
  }

  &item{
    @extend .o-list-inline__item;
    margin-left: $inuit-global-spacing-unit-tiny;
    &:first-child{
      margin-left: 0;
    }
  }
  &link{
    display: block;
    padding: $inuit-global-spacing-unit-tiny;

  }
}